
.todo-list-container {
    background-color: #ffffff;
    /* خلفية بيضاء للحاوية */
    border-radius: 12px;
    /* حواف دائرية ناعمة */
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
    /* ظل خفيف لإضافة عمق */
    
    width: 100%;
    max-width: 600px;
    /* عرض أقصى لضمان القراءة */
}

/*
  ====================================
  تصميم عنصر المهمة الواحدة
  ====================================
*/

.task-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 8px;
    transition: all 0.3s ease;
    /* للانتقالات السلسة */
    border: 1px solid #e0e0e0;
}

/*
  ====================================
  تصميم حالة المهمة (مكتملة/غير مكتملة)
  ====================================
*/

/* المهمة غير المكتملة (قيد التقدم) */
.InCompletedTask {
    background-color: #fcebeb;
    /* خلفية خفيفة لتنبيه بضرورة العمل */
    color: #333;
    /* النص نفسه بدون تغيير كبير */
}

/* المهمة المكتملة */
.CompletedTask {
    background-color: #e6f7ee;
    /* خلفية خضراء فاتحة للإشارة إلى الإنجاز */
    color: #707070;
    /* لون رمادي باهت للنص */
    text-decoration: line-through;
    /* خط يمر عبر النص */
    opacity: 0.8;
}

.task-info {
    flex-grow: 1;
    margin-right: 20px;
    font-size: 16px;
}


.task-actions button {
    padding: 8px 15px;
    margin-left: 8px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.2s, transform 0.1s;
}

.task-actions .complete-btn {
    background-color: #4CAF50;

    color: white;
}

.task-actions .Uncomplete-btn {
    background-color: rgb(146, 103, 103);

    color: black;
}

.task-actions .complete-btn:hover {
    background-color: #45a049;
    transform: translateY(-1px);
}

.task-actions .delete-btn {
    background-color: #f44336;
    /* أحمر للحذف */
    color: white;
}

.task-actions .delete-btn:hover {
    background-color: #e53935;
    transform: translateY(-1px);
}

/*
  ====================================
  تصميم إضافة مهمة جديدة
  ====================================
*/

.add-task-form {
    display: flex;
    margin-top: 30px;
    gap: 10px;
    /* مسافة بين العناصر */
}

.add-task-form input[type="text"] {
    flex-grow: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

.add-task-form button {
    padding: 10px 20px;
    background-color: #2196F3;
    /* أزرق أساسي */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.2s;
}

.add-task-form button:hover {
    background-color: #1e88e5;
}